<template>
    <div class="list">
        <ListItem v-for="topic in topics" v-bind:topic="topic" v-on:clicked="choose" v-bind:isChoosen="choosen === topic"></ListItem>
    </div>
</template>

<script>
import ListItem from './ListItem.vue'

export default {
    name: 'list',
    components: {
        ListItem
    },
    props: ['topics', 'search', 'choosen'],
    methods: {
        choose: function(topic) {
            this.$emit('choose', topic);
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.list {
    overflow-y: auto;
}
</style>



