<template>
    <div class="calc">
        <div v-show="data.html" v-html="data.html"/>
        <div class="row">
            <div class="col-4">
                <VarInput v-for="name in data.input" v-bind:name="name" v-on:var="handleVar" />
                <button class="btn btn-outline-primary" @click="calc">Подсчитать</button>
                <p class="result">Ответ: {{result}}</p>
            </div>
            <div class="col d-flex align-items-center justify-content-center">
                <div v-show="data.formula" v-html="data.formula"/>
            </div>
        </div>
    </div>
</template>

<script>
import VarInput from './VarInput.vue';

export default {
    name: 'calculator',
    props: ['data'],
    components: {
        VarInput
    },
    data: function(){
        return {
            result: "Нажми на кнопку!"
        }
    },
    beforeUpdate: function() {
        if(this.data.input){
            var self = this;
            this.data.input.forEach((el)=>{
                eval(`self.${el} = 0`);
            });
        }
        if(this.data.js){
            var self = this;
            eval(`self.fun = ${this.data.js}`);
        }
    },
    methods: {
        calc: function() {
            let obj = {};
            var self = this;
            this.data.input.forEach((el)=>{
                obj[el] = eval("self."+el);
            });
            this.result = this.fun(obj);
        },
        handleVar(obj){
            var name = obj.name;
            var val = obj.val;
            var self = this;
            eval(`self.${name} = ${val}`);
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.result {
    color: #2ecc71 !important;
    margin-top: 0.5em;
}
.calc {
    border: solid 3px #2ecc71;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 5px;
}
</style>



