<template>
    <div class="item d-flex align-items-center" v-bind:class="{ active: isChoosen }" @click="click">
        {{topic.name}}
    </div>
</template>

<script>
export default {
    name: 'listitem',
    props: ['topic', 'isChoosen'],
    methods: {
        click: function() {
            this.$emit("clicked", this.topic);
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .item {
        cursor: pointer;
        /*height: 2em;*/
        width: 100%;
        color: #2ecc71;
        border-bottom: solid 1px #2ecc71;
        font-size: 1em;
        margin-bottom: 5px;
        padding: 5px;
    }
    .active {
        color: white !important;
        background-color: #2ecc71 !important;
        border-radius: 5px;
        border-bottom: none;
    }
</style>



