<template>
    <div class="container">
        <TopicContent v-show="data" v-bind:data="data"/>
    </div>
</template>

<script>
import axios from 'axios';
import TopicContent from './TopicContent.vue';

export default {
    name: 'container',
    props: ['topic'],
    components: {
        TopicContent
    },
    data: function(){
        return {
            data: {
                html: "<h1>Загрузка...</h1>"
            },
            url: null
        }
    },
    created: function() {
        this.downloadData();
    },
    beforeUpdate: function() {
        if(this.url != this.topic.url) this.downloadData();
        console.log("beforeUpdate");
    },
    updated: function() {
        window.MathJax.Hub.Typeset();
    },
    methods: {
        downloadData: function(){
            this.url = this.topic.url;
            axios.get('static/docs/'+this.url, { headers: {'Accept': 'application/json'}})
            .then(function(res){
                console.log(res.data);
                this.data = res.data;
            }.bind(this)).catch((err)=>{
                console.log(err);
            });
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container {
    width: 100%;
    height: 100%;
    /*overflow-y: auto;*/
}
</style>



