<template>
    <div class="input-group varinput">
        <span class="input-group-addon">{{name}}</span>
        <input type="text" class="form-control" v-model="val"/>
    </div>
</template>

<script>
export default {
    name: 'varinput',
    props: ['name'],
    data: function(){
        return {
            val: 0
        }
    },
    watch: {
        val: function() {
            this.val = +this.val;
            if(isNaN(this.val)) this.val = 0;
            this.$emit("var", {
                name: this.name,
                val: this.val
            });
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.varinput {
    margin-bottom: 1em;
}
</style>



