<template>
  <div id="app">
    <div class="container-fluid h-100">
        <div class="row panel">
            <div class="col-3 d-flex align-items-center" id="searchContainer">
                <input class="form-control" type="text" placeholder="Поиск" v-model="search"/>
            </div>
            <div class="col d-flex align-items-center justify-content-between">
                <p id="currentTopic" class="text-muted">{{choosen.name}}</p>
                <h3 id="homeButton" @click="goHome">Домой</h3>
            </div>
        </div>
        <div class="row content h-100">
            <div class="col-3 h-100" id="listBox">
                <List v-bind:topics="filteredTopics" v-on:choose="choose" v-bind:choosen="choosen"></List>
            </div>
            <div class="col h-100" id="contentBox">
                <Container v-show="choosen" v-bind:topic="choosen"/>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import List from './components/List.vue'
import Container from './components/Container.vue'

export default {
    name: 'app',
    components: {
        List,
        Container
    },
    data: function() {
        return {
            choosen: {
                name: "Home",
                url: "index.json"
            },
            search: null
        }
    },
    props: ['topics'],
    beforeMount: function() {
        this.filter();
    },
    watch: {
        search: function() {
            this.filter();
        }
    },
    methods: {
        choose: function(topic) {
            this.choosen = topic;
        },
        goHome: function() {
            this.choosen = {
                name: "Home",
                url: "index.json"
            };
        },
        filter: function() {
            this.filteredTopics = [];
            if(this.search){
                this.filteredTopics = this.topics.filter((el) => {
                    return el.name.toLowerCase().includes(this.search);
                })
            } else {
                this.filteredTopics = this.topics;
            }
        }
    }
}
</script>

<style>
body, html {
    height: 100vh;
    overflow-y: hidden;
}
#app {
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  /*height: 100vw;*/
  height: 100%;
  /*overflow-y: hidden;*/
}
#searchContainer {
    border-right: solid 1px #2ecc71;
}
#homeButton {
    padding: 0;
    margin: 0;
    margin-left: 0.5em;
    cursor: pointer;
    color: #2ecc71;
}
#currentTopic {
    margin: 0;
    margin-left: 3em;
}
#listBox {
    border-right: solid 1px #2ecc71;
    padding: 10px;
    overflow-y: auto;
}
#contentBox {
    /*padding: 0;*/
    overflow-x: auto;
}
.content {
    /*padding: 5px;*/
    height: 100%;
}
.panel {
    height: 3em;
    padding: 3px;
    padding-left: 0;
    padding-right: 0;
    /*border-bottom:*/

    -webkit-box-shadow: 0px 1px 15px -1px rgba(0,0,0,0.24);
    -moz-box-shadow: 0px 1px 15px -1px rgba(0,0,0,0.24);
    box-shadow: 0px 1px 15px -1px rgba(0,0,0,0.24);
}
</style>



